
import { useContext, useRef} from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import Main from '../Main/Main';
import React from 'react';
import Context from '../Context/Context';
import { useNavigate } from 'react-router';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import getPosts from '../hooks/getPosts';
import { currentUserReducer } from '../store/currentUser';

function General() {
  const {postsDate} = useContext(Context)
let dispatch = useDispatch()
let navigate = useNavigate()
useEffect(()=>{
  dispatch({type: 'REMOVE_TR'})
if(!JSON.parse(localStorage.getItem('postsDate'))?.length){
  console.log(postsDate);
  getPosts(commit)
}
else{
  commit(JSON.parse(localStorage.getItem('postsDate')))
}
    
  
  

}, [])

function commit(arg){
  dispatch({type: 'SET_P', item: arg})
  dispatch({type: 'SET_I_P', item: true})
}




  return (
    <>
        <Contacts/>
        <Main/>
        {window.screen.width > 500? <Bio />: ''}
    </>
  );
}

export default General;
