import axios from 'axios';
async function setUsers(usersDate){
    try{
        let url = "https://getpantry.cloud/apiv1/pantry/48772c43-095e-44c0-b987-edab68e5bbda/basket/usersDate";
        axios.post(url,{usersDate} );
    }catch(e){
        console.log(e);
    }
}
export default setUsers;
