
import React, { useContext} from "react"
import Context from "../Context/Context"
import MobileBio from "./BioBloks/MobileBio"
import BioPc from "./BioBloks/BioPc"

function Bio(){
    const { currentUser, currentPerson, screenWidth, isReg} = useContext(Context)
 
    if(isReg && currentPerson.postName) return screenWidth > 500 ? <BioPc/> : <MobileBio/>

    else if(currentUser.name) return screenWidth > 500 ? <BioPc/> : <MobileBio/>
    
}
export default Bio